<template>
  <div id="app">
    <div class="content2" v-show="isshow">
      <div class="add">
        <div class="header">
          <span style="float: left;">新增参数</span>
          <span style="float: right; cursor: pointer;" @click="show">X</span>
        </div>
        <div
          class="zhong"
          style="margin-top: 50px;  text-align: left;  width: 100%;" 
        >
          <el-form :model="form">
                <el-form-item label="试验电厂" :label-width="formLabelWidth">
                    <el-select v-model="form.factory" placeholder="请选择" style="width:486px;height:32px;">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="试验设备" :label-width="formLabelWidth">
                    <el-select v-model="form.equipment" placeholder="请选择" style="width:486px;height:32px;">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item label="报告模板" :label-width="formLabelWidth">
                    <el-select v-model="form.module" placeholder="请选择" style="width:486px;height:32px;">
                        <el-option label="区域一" value="shanghai"></el-option>
                        <el-option label="区域二" value="beijing"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <span slot="footer" class="dialog-footer" style="display:block; text-align:center; margin-top:40px;">
                <router-link to="./testMessageList"><el-button type="primary" @click="centerDialogVisible = false">保存</el-button></router-link>
            </span>
        </div>
      </div>
    </div>

    <div class="header">
      <div class="header_con">
        <el-breadcrumb separator="/" style="font-size:16px;">
          <el-breadcrumb-item>报告管理</el-breadcrumb-item>
          <el-breadcrumb-item><label for="" style="color:black;">实验数据列表</label></el-breadcrumb-item>
        </el-breadcrumb>
        <router-link to="./reportAfteradd"><button @click="goBack">返回</button></router-link>
      </div>
    </div>
    <div class="section">
      <div class="section_con">
        <div class="section_con_main">
          <div class="img">
            <img src="../../../src/assets/img/img2.jpg" alt />
          </div>
          <button @click="show">录入实验数据</button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "canshuKong",
  data() {
    return {
      isshow: false,
      iptIsshow1: false,
      iptIsshow2:false,
      goBack() {
        history.back();
      },
      dialogTableVisible: false,
      dialogFormVisible: true,
      centerDialogVisible: false,
      form: {
        name: "",
        radio: "",
        input: "",

        /**/
        dingxing1: "",
        dingxing2: "",
        factory: '',
                    equipment: '',
                    module: ''
      },
      formLabelWidth: "120px"
    };
  },
  methods: {
    show() {
      this.isshow = !this.isshow;
    },
    dingxing1() {
      this.iptIsshow1 = true;
      this.iptIsshow2 = false;
    },
    dingxing2() {
      this.iptIsshow2 = true;
      this.iptIsshow1 = false;
    }
  }
};
</script>

<style lang="" scoped>
html,
body {
  width: 100%;
  height: 100%;
  margin: 0;
  background: rgba(240, 242, 245, 1);
}

ul,
li {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  margin: 0;
  padding: 0;
  border: 1px solid transparent;
  outline: none;
  color: #ffffff;
  cursor: pointer;
}

img {
  width: 100%;
  height: 100%;
}

.header .header_con {
  display: flex;
  justify-content: space-between;
  align-items: center;
  box-sizing: border-box;
  width: 100%;
  height: 54px;
  background: rgba(255, 255, 255, 1);
  border-radius: 2px;
  padding: 0 24px;
  text-align: center;
  line-height: 54px;
}

.header .header_con .el-breadcrumb {
  display: flex;
  justify-content: flex-start;
}

.header .header_con .el-breadcrumb__item {
  text-align: center;
  line-height: 54px;
}

.header .header_con .el-breadcrumb__item:nth-child(2) .el-breadcrumb__inner {
  font-weight: 900;
}

.header .header_con button {
  display: block;
  width: 65px;
  height: 32px;
  background: rgba(24, 144, 255, 1);
  border-radius: 4px;
  /* margin: 11px 0 11px 1450px; */
}

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 15px;
}

.section .section_con {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 96.6%;
  top:120px;
  height: 85%;
  position: absolute;
  background: rgba(255, 255, 255, 1);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.12);
  border-radius: 2px;
}
.section .section_con .section_con_main {
  width: 90px;
  height: 120px;
}
.section .section_con .img {
  /* margin: 280px 0 0 652px; */
  width: 88px;
  height: 64px;
}

.section .section_con button {
  display: inline-block;
  width: 90px;
  height: 32px;
  margin-top: 10px;
  background: rgba(45, 132, 239, 1);
  border-radius: 4px;
}
.content2 {
  position: absolute;
  width: 97%;
  left:1.5%;
  top:120px;
  height: 85%;
  /* background: black; */
  background-color: rgba(0, 0, 0, 0.5);
  z-index: 2;
}

.add {
  position: absolute;
  width: 650px;
  height: 290px;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  padding: 15px;
  background: white;
}
.content2 .el-button {
  padding: 3px;
  width: 80px;
  height: 30px;
  /* text-align: center; */
  /* line-height: px; */
}
</style>



